.navbar img{
  max-width: 100px;
}

.navbar{
  display: flex;
  box-shadow: rgba(0,0,0, .15) 0px -2px 10px 0px;
  justify-content: space-between;
  align-items: center;
  padding: .5em 2em;
  background-color: rgb(254, 212, 196);
}

.links_list{
  display: flex;
  list-style: none;
}

.links_list li{
  margin-right: 1em;
}

.links_list li a{
  padding: .4em .6em;
}

.active{
  background-color: rgb(73, 24, 11);
  color: white;
}
